import { Component, OnInit } from '@angular/core';

import { Party, PartySummary }        from './party';
import { PartyService } from './party.service';

@Component({
  moduleId: module.id,
  selector: 'my-dashboard',
  templateUrl: './team.component.html',
  styleUrls: [ './team.component.css' ]
})
export class TeamComponent implements OnInit {
  parties: Party[] = [];
  partySummary : PartySummary[] = [];
  selectedTeam : String;
  selectedParties :Party[] = [];
  party : Party = new Party;

  constructor(private partyService: PartyService) { }

  ngOnInit(): void {
    //this.partyService.getParties()
      //.then(parties => this.parties = parties);
    this.partyService.getPartySummary()
       .then(partySummary => { this.partySummary = partySummary;})
    this.partyService.getParties('WorkersComp')
       .then(parties => { 
         this.selectedParties = parties;
         this.selectedTeam = 'WorkersComp';
         this.party.Team = 'WorkersComp';
        })
  }

  refreshSelectedSummary(team: string): void {
    team = team.trim();
    if (!team) { return; }
    this.partyService.getParties(team)
      .then(parties => {
        this.selectedTeam = team;
        this.party.Team = team;
        this.selectedParties = parties;
      });
  }
  doRefresh(){
   this.refreshSelectedSummary(this.selectedTeam.trim());
   this.partyService.getPartySummary()
       .then(partySummary => { this.partySummary = partySummary;})
  }

  create(): void {
    this.partyService.create(this.party)
      .then(() => {alert('New contact has been created');
                   this.doRefresh();});
  }

}



