import { Component , OnInit }          from '@angular/core';
import { TeamService }                 from './team.service';
import {OffwiredService}               from './offwired.service';
import { Project }                     from './work-item';

@Component({
  moduleId: module.id,
  selector: 'my-app',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnInit{
  title = 'Offwired';
  projects : Project[] = [];

  constructor(private teamService: TeamService, private offwiredService: OffwiredService) {
    /*
    window.onbeforeunload = function(e) {
      offwiredService.closeSession().then();
      alert('The local server will be closed');
    };
    */
   }

  ngOnInit(): void {
    this.teamService.getProjects()
      .then(projects => this.projects = projects);
  }
}



