import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

import 'rxjs/add/operator/toPromise';

import { Party, PartySummary } from './party';

@Injectable()
export class PartyService {

  private headers = new Headers({'Content-Type': 'application/json'});
  private partiesUrl = '/partiesapi';  // URL to web api
  private partyURL = '/partyapi';
  private partySummaryURL = '/partySummaryapi';

  constructor(private http: Http) { }

  getParties(team: String): Promise<Party[]> {
    const url = `${this.partiesUrl}/${team}`;
    return this.http.get(url)
               .toPromise()
               .then(response => response.json() as Party[])
               .catch(this.handleError);
  }

  getPartySummary(): Promise<PartySummary[]> {
    return this.http.get(this.partySummaryURL)
               .toPromise()
               .then(response => response.json().data as PartySummary[])
               .catch(this.handleError);
  }


  getParty(id: string): Promise<Party> {
    /*
    if (Number.isNaN(id)){
          id = 11;
    }*/
    const url = `${this.partyURL}/${id}`;
    return this.http.get(url)
      .toPromise()
      .then(response => { return response.json()})
      .catch(this.handleError);
  }
  /*

  delete(id: number): Promise<void> {
    const url = `${this.partiesUrl}/${id}`;
    return this.http.delete(url, {headers: this.headers})
      .toPromise()
      .then(() => null)
      .catch(this.handleError);
  }
  */
  create(party: Party): Promise<Party> {
    return this.http
      .post(this.partyURL, JSON.stringify(party), {headers: this.headers})
      .toPromise()
      .then(res => party) //not sure if this works as compared to ()=> party
      .catch(this.handleError);
  }
  
  update(party: Party): Promise<Party> {
    return this.http
      .put(this.partyURL, JSON.stringify(party), {headers: this.headers})
      .toPromise()
      .then(() => party)
      .catch(this.handleError);
  }
  
  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); // for demo purposes only
    return Promise.reject(error.message || error);
  }
}




