import 'rxjs/add/operator/switchMap';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router }            from '@angular/router';
import { ActivatedRoute, Params } from '@angular/router';
import { Location }               from '@angular/common';

import { WorkItem, WorkStatus }                from './work-item';

import {DragulaModule, DragulaService} from 'ng2-dragula/ng2-dragula';

import { TeamService }         from './team.service';
//import * as $ from 'jquery';
import { ModalDirective } from 'ng2-bootstrap/modal';

@Component({
  moduleId: module.id,
  viewProviders: [DragulaService],
  selector: 'my-hero3es',
  templateUrl: './work-items.component.html',
  styleUrls: [ './work-items.component.css' ]
})
export class WorkItemsComponent implements OnInit{
  todo  : WorkItem[];
  doing : WorkItem[];
  done  : WorkItem[];
  workStatus : WorkStatus;
  selectedWorkItem: WorkItem;
  currentProjectName : string;

  constructor(
    private dragulaService:DragulaService,
    private teamService: TeamService,
    private route: ActivatedRoute,
    private location: Location,
    private router: Router) {
      dragulaService.drop.subscribe((value) => {
        //console.log(value);
        const [bagName, e, el] = value;
        //console.log(bagName);
        //console.log(e);
        //console.log(e.attributes.Id);
        //console.log(e.attributes.Name);
      //this.onDropModel(value.slice(1));
        });
      dragulaService.dropModel.subscribe((value) => {
        alert('drop model event happened');
        //console.log(value);
        //console.log(this.workItems[0]);
        });

     }

     @ViewChild('childModal') 
      public childModal:ModalDirective;
     
      public showChildModal():void {
        this.childModal.show();
      }
     
      public hideChildModal():void {
        this.childModal.hide();
      }

     fetchData() : void {
        this.route.params
            .switchMap((params: Params) =>{ this.currentProjectName = params['id']; return this.teamService.getWorkItems(params['id'])})
            .subscribe(workStatus => { 
                                this.workStatus = workStatus;
                                this.todo  = workStatus.todo;
                                this.doing = workStatus.doing;
                                this.done  = workStatus.done;
                                });
     }

     moveToDoing(workItem: WorkItem): void {
      this.teamService
          .changeStatus(this.currentProjectName, 'Doing',workItem)
          .then(() => { 
            this.fetchData();
            //$('#modal-success').modal('show');
          });
    }
    markAsDone(workItem: WorkItem): void {
      this.teamService
          .changeStatus(this.currentProjectName, 'Done',workItem)
          .then(() => { this.fetchData();
          });
    }
    add(taskName: string, storyPoints:number): void {
     taskName = taskName.trim();
     this.teamService.create(taskName,storyPoints,this.currentProjectName)
      .then(workItem => {
        this.fetchData();
      });

    }
 /*
  getHeroes(): void {
    this.teamService
        .getHeroes()
        .then(heroes => this.heroes = heroes);
  }

  add(name: string): void {
    name = name.trim();
    if (!name) { return; }
    this.heroService.create(name)
      .then(hero => {
        this.heroes.push(hero);
        this.selectedHero = null;
      });
  }

  delete(hero: Hero): void {
    this.heroService
        .delete(hero.id)
        .then(() => {
          this.heroes = this.heroes.filter(h => h !== hero);
          if (this.selectedHero === hero) { this.selectedHero = null; }
        });
  }
 */
  ngOnInit(): void {
    this.fetchData();
  }
  //ngAfterViewInit() {
    /*
    jQuery(this.input.nativeElement).datepicker({
      onSelect: (value) => {
        this.value = value;

        this.dateChange.next(value);
      }
    })
    .datepicker('setDate', this.value);
    */
 // }

  /*
  onSelect(hero: Hero): void {
    this.selectedHero = hero;
  }

  gotoDetail(): void {
    this.router.navigate(['/detail', this.selectedHero.id]);
  }
  */
}



